
import React, { useState } from 'react';
import { AgGridReact } from 'ag-grid-react';
import { Button, Container } from '@material-ui/core';

import Heading from '../utils/Heading';

import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';


const Grid = props => {
  const [gridApi, setGridApi] = useState(null);

  const onGridReady = params => setGridApi(params.api);

  const onBtnExport = () => gridApi.exportDataAsCsv();

  const getOnlineStatus = () => ('<span class="spriteOnlineStatusUp"></span>');

  const getRowClass = params => (params.node.rowIndex % 2 === 1 ? 'gridAlternateRow' : '');

  const columnDefs = [
    { headerName: 'Store Name', field: 'storeName', sortable: true, filter: true, minWidth: 200, maxWidth: 200, tooltipField: 'storeName' },
    { headerName: 'Till Number', field: 'tillNumber', sortable: true, filter: true, maxWidth: 135 },
    { headerName: 'Status', field: 'tillStatus', sortable: false, filter: false, maxWidth: 80, cellRenderer: getOnlineStatus },
    { headerName: 'Till Address', field: 'tillIpAddress', sortable: true, filter: true, maxWidth: 130 },
    { headerName: 'Till Status', field: 'tillStatus', sortable: true, filter: true, maxWidth: 165 },
    { headerName: 'Store Address', field: 'storeAddressLink', sortable: false, filter: true, maxWidth: 220, minWidth: 220, tooltipField: 'storeAddressLink' }
  ];

  return (
    <>
      <Heading title="Halfords All Stores Till Status"></Heading>
      <div className="buttonContainers">
        <Button onClick={() => onBtnExport()} className="tillButtons" justify="flex-end">
          Export Data
        </Button>
      </div>
      <div className="ag-theme-alpine tillStatusGrid">
        <AgGridReact rowData={props.rowData} onGridReady={onGridReady} columnDefs={columnDefs} getRowClass={getRowClass} />
      </div>
    </>
  );
};

export default Grid;
