import { NavLink } from 'react-router-dom';
import { AppBar, IconButton, Toolbar, List, ListItem, ListItemText, Container } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles({
    appBarBackground: {
        backgroundColor: '#212121'
    },
    navbarDisplayFlex: {
        display: `flex`,
        justifyContent: `space-between`,
        alignItems: `center`
    },
    navDisplayFlex: {
        display: `flex`,
        justifyContent: `space-between`
    },
    linkText: {
        textDecoration: `none`,
        textTransform: `uppercase`,
        color: `#fff`,
        fontWeight: 500
    }
});

const Header = () => {
    const classes = useStyles();

    const navLinks = [
        { title: `Home`, path: `/` },
        { title: `Dashboard`, path: `/dashboard` }
    ];

    return (
        <AppBar position="relative" className={classes.appBarBackground}>
            <Toolbar className="topNavigation">
                <Container maxWidth="lg" className={classes.navbarDisplayFlex}>
                    <IconButton edge="start" color="inherit">
                        <span className="logoSpan"></span>
                    </IconButton>

                    <List component="nav" aria-labelledby="main navigation" className={classes.navDisplayFlex}>
                        {navLinks.map(({ title, path }) => (
                            <NavLink exact={title === 'Home'} to={path} key={title} activeClassName="active" className={classes.linkText} >
                                <ListItem button>
                                    <ListItemText primary={title} />
                                </ListItem>
                            </NavLink>
                        ))}
                    </List>
                </Container>
            </Toolbar>
        </AppBar>
    );
}

export default Header;
