import { HashRouter, Route, Switch } from 'react-router-dom';

import Header from './components/Header';
import Dashboard from './pages/Dashboard';
import Home from './pages/Home';


const App = () => {
  return (
    <HashRouter>
      <Header />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/dashboard">
          <Dashboard />
        </Route>
      </Switch>
    </HashRouter>
  );
}

export default App;
