import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Grid, Container } from '@material-ui/core';

import Filters from '../components/Filter';
import Chart from '../components/Chart';
import AgGrid from '../components/Grid';
import Loading from '../utils/Loading';
import { TILL_STATUS_URL, TOTAL_TILL_COUNT } from '../constants/Constants';


const Dashboard = () => {
    const [open, setOpen] = useState(true);
    const [isError, setIsError] = useState(false);
    const [tillStatusData, setTillStatusData] = useState([]);
    const [percent, setPercent] = useState(0);

    useEffect(() => {
        axios.get(TILL_STATUS_URL).then(response => {
            const effectedTillCount = response.data.length;
            const effectedTillPercentage = effectedTillCount / TOTAL_TILL_COUNT;
            setPercent(effectedTillPercentage);
            setTillStatusData(response.data);
            setOpen(false);
        }).catch(error => setIsError(true))
    }, []);

    return (
        <>
            <Loading open={open} isError={isError} />
            <Filters />
            <Container maxWidth="lg" className="mainContainer">
                <Grid container spacing={3}>
                    <Grid item xs={9} className="gridWrapper">
                        <AgGrid rowData={tillStatusData} />
                    </Grid>
                    <Grid item xs={3} className="chartWrapper">
                        <Chart percent={percent} />
                    </Grid>
                </Grid>
            </Container>
        </>
    );
}

export default Dashboard;
